.wrapper .delete-btn{cursor:pointer;background-color:#ed1c24;border:0;color:#fff;font-size:20px;line-height:20px;padding:2px 5px;position:absolute;left:29%;opacity:0;transition:opacity 0.3s ease}.transaction-list .wrapper .expense-amount{color:#ed1c24;padding:20px}.transaction-list .wrapper li:hover .delete-btn{opacity:1}.add-transaction{padding:20px}.add-transaction .title{margin:20px 0px;border-bottom:5px solid #06c167;color:#616161;font-weight:600;letter-spacing:1px;text-align:left}.add-transaction .wrapper{display:flex;flex-direction:column;width:100%}.add-transaction .row{display:flex;flex-direction:row;justify-content:space-between;margin:15px;width:100%}.add-transaction .input{border:solid 1px #06c167;border-radius:5px;padding:15px;width:50%}.add-transaction .button{border:solid 1px #06c167;border-radius:5px;background-color:#06c167;color:white;font-size:20px;font-weight:bold;padding:15px;margin:20px;width:50%}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

